*,
*::before,
*::after {
  margin: 0;
  padding: 0;
  box-sizing: inherit; }

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth; }
  @media only screen and (max-width: 75em) {
    html {
      font-size: 50%; } }

body {
  line-height: 1.6;
  font-weight: 300;
  font-family: "Lato", sans-serif;
  color: #111;
  min-height: 100vh;
  min-width: 40rem;
  display: flex;
  flex-direction: column;
  font-family: "Lato", sans-serif; }

::selection {
  background-color: #febd69;
  color: #fff; }

.home {
