    flex-wrap: wrap;
    justify-content: center;
    width: 100%;
    height: 100%; }
  .home h1 {
    font-size: 4.5rem;
    line-height: 6rem;
    letter-spacing: 0.15rem; }
    @media only screen and (max-width: 43.75em) {
      .home h1 {
