.heading-secondary {
  font-size: 2.25rem;
  text-transform: uppercase;
  font-weight: 700;
  background-image: linear-gradient(to bottom, #f7dfa5, #f0c14b);
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  letter-spacing: 0.1rem;
  line-height: 1.3;
  display: inline-block; }

.ma-bt-md {
  margin-bottom: 3rem !important; }

.ma-bt-lg {
  margin-bottom: 3.5rem !important; }

.form__group:not(:last-child) {
  margin-bottom: 3rem; }

.form__photo-upload {
  display: flex;
  align-items: center;
  font-size: 1.6rem; }

.form__label {
  display: block;
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 0.75rem; }

.form__input {
  display: block;
  font-family: inherit;
  font-size: 1.5rem;
  color: inherit;
  padding: 1.25rem 1.75rem;
  border: 1px solid #e6e6e6;
  width: 100%;
  background-color: #fff;
  transition: all 0.3s;
  border-radius: 4px;
  box-sizing: border-box; }

.form__input:focus {
  outline: none;
  border: 1px solid #55c57a; }

.form__input:focus:invalid {
  border: 1px solid #ff7730; }

.form__input::-webkit-input-placeholder {
  color: #bbb; }

.btn,
.btn:link,
.btn:visited {
  font-size: 1.4rem;
  padding: 1.4rem 2.5rem;
  border-radius: 0.4rem;
  border: none;
  display: inline-block;
  text-decoration: none;
  position: relative;
  transition: all 0.3s;
  font-weight: 400;
  backface-visibility: hidden;
  cursor: pointer; }

.btn:active {
  box-shadow: 0 0.5rem 1rem rgba(0, 0, 0, 0.15); }

.btn:focus {
  outline: none;
  box-shadow: 0 0 3px 2px rgba(228, 121, 17, 0.5); }

.btn--primary {
  background: linear-gradient(to bottom, #f7dfa5, #f0c14b);
  color: inherit; }

.btn--primary:hover {
  background: linear-gradient(to bottom, #f0c14b, #f0c14b); }

.btn--green {
  background-color: #55c57a;
  color: #fff; }

.btn--green:hover {
  background-color: #2e864b; }

.btn--green::after {
  background-color: #55c57a; }

.btn--red {
  background-color: #f44336;
  color: #fff; }

.btn--red::after {
  background-color: #f44336; }

.btn--red:hover {
  background-color: #e8190a; }

.btn--search {
  border-radius: 1rem;
  background-color: #5e656c;
  color: #fff; }

.btn--search:hover {
  background-color: #232f3e; }

.btn--margin {
  margin: 0rem 1.1rem; }

.user-view {
  background-color: #fff;
  max-width: 110rem;
