.form__user-photo {
  height: 14rem;
  width: 14rem;
  border-radius: 50%;
  margin-right: 2rem; }

.form__review-photo {
  height: 28rem;
  width: 28rem;
  margin-right: 2rem; }

.form__upload {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1; }

.form__upload:focus + label {
  outline: 3px solid #55c57a;
  outline-offset: 3px; }

.form__upload + label {
  color: inherit;
  display: inline-block;
  text-decoration: none;
  border-bottom: 1px solid #111;
  padding: 3px;
  transition: all 0.2s;
  cursor: pointer; }

.form__upload + label:hover {
  background: linear-gradient(to bottom, #f7dfa5, #f0c14b);
  border-bottom: 1px solid #f0c14b;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.15);
  transform: translateY(-2px); }

.right {
  text-align: right !important; }

.space-between {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.text-link {
  text-decoration: none;
  font-size: 1.5rem;
  color: #0066c0; }

