header {
  background-color: #232f3e;
  height: 8rem;
  padding: 0 5rem;
  display: flex;
  align-items: center;
  justify-content: space-between; }
  @media only screen and (max-width: 28em) {
    header {
      padding: 0 2rem; } }

.header__nav {
  display: flex;
  align-items: center; }

.header__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 1.5rem; }

.header__logo:hover {
  cursor: pointer; }

.header__image {
  height: 2.3rem; }

.header__nav--link {
  font-size: 1.6rem;
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  display: inline-flex;
  align-items: center; }

.header__nav--link:hover {
  color: #febd69; }

.header__img {
  height: 4rem;
  width: 4rem;
  border-radius: 50%;
  margin-right: 0.7rem;
  margin-left: 3rem; }
