footer {
  padding: 6rem 4rem;
  font-size: 1.4rem;
  color: #fff;
  display: flex;
  background-color: #37475a;
  align-items: center;
  justify-content: center; }
  @media only screen and (max-width: 28em) {
    footer {
      padding: 4rem 2rem; } }

.footer__wrapper {
  display: flex;
  flex-direction: row;
  align-items: center;
  width: 90rem;
  justify-content: space-between; }

.footer__logo {
  display: flex;
  flex-direction: column;
  align-items: center;
  line-height: 1.5rem; }

.footer__logo:hover {
  color: #febd69; }

.footer__nav--link {
  font-size: 1.4rem;
  color: #fff;
  cursor: pointer;
  text-decoration: none;
  display: inline-flex;
  align-items: center; }

.footer__image {
  height: 2rem; }

.footer__nav {
  display: flex;
  list-style: none;
  cursor: pointer; }

.footer__nav li {
  margin-left: 1.5rem; }

.footer__nav li:hover {
  color: #febd69; }

.footer__right {
  display: flex;
  flex-direction: column;
  align-items: flex-end; }
